                          </v-col>
                        </v-row>
                      </v-col>
            </v-row>
            <v-row align="center" justify="center" v-if="userPrediction > 0">
                      <v-col cols="12" style="border-radius: 5px" class="tertiary" >
                        <v-row class="text-left" align="center">
                          <v-col cols="8">
                            <v-row align="center">
                              <v-col cols="4">
                                <v-icon
                                    x-large
                                    color="primary"
                                >
                                  mdi-briefcase
                                </v-icon>
                              </v-col>
                              <v-col cols="8">
                                <div>Your Locked Price</div>
                                <!--<div class="caption">{{item.priceDateTime}} UTC</div>-->
                              </v-col>
                            </v-row>
                          </v-col>
                          <v-col cols="4" class="text-h6 text-right">
                            {{userPrediction}}
                          </v-col>
                        </v-row>
                      </v-col>
            </v-row>
            <v-row align="center" justify="center" v-if="userPrediction == 0">
                <v-col cols="12" style="border-radius: 5px" class="tertiary" >
                        <v-row class="text-left" align="center">
                          <v-col cols="6">
                            <v-row align="center">
                              <v-col cols="4">
                                <v-icon
                                    x-large
                                    color="primary"
                                >
                                  mdi-bullseye-arrow
                                </v-icon>
                              </v-col>
                              <v-col cols="8">
                                <div>Predict Price</div>
                                <!--<div class="caption">{{item.priceDateTime}} UTC</div>-->
                              </v-col>
                            </v-row>
                          </v-col>
                          <v-col cols="3" class="text-h6 text-right">
                            <v-text-field v-model="userPredictionInput" label="Bull's Eye Prediction" type="number"></v-text-field>
                          </v-col>
                          <v-col cols="3">
                              <v-btn @click="makePrediction">Submit</v-btn>
